body {
  font-family: 'Lato', sans-serif;
}

a {
  background-color: blue;
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
}

.selected {
  background-color: #8080ff;
}

section {
  margin: 20px;
}

img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin: 10px;
}

h2 {
  margin-bottom: 5px;
}

ul {
  list-style-type: none;
  padding-left: 0;
}

li {
  display: flex;
  width: 300px;
  height: 120px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin: 10px 0;
}

li > div {
  display: flex;
  flex-direction: column;
}

.panel {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 200px;
  color: white;
}

.panel > p {
  margin: 0;
}

.panel > a {
  margin: 10px 0;
  background-color: white;
  color: black;
  cursor: pointer;
}

.MuiDrawer-paper {
  background: blue !important;
}

.modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: white;
  width: 350px;
  height: 290px;
  position: absolute;
  left: calc(50% - 100px);
  top: calc(50% - 200px);
}

.modal > p {
  margin: 0;
}

.modal > a {
  margin: 10px 0;
}

.panel > .links > a {
  width: 160px;
  cursor: pointer;
}

.panel > .links {
  margin-bottom: 20px;
}

.panel > .food {
  line-height: 38px;
}

.modal > .food {
  line-height: 36px;
}

.modal > .links {
  display: flex;
  margin: 10px;
}

.modal > .links > a {
  font-size: 12px !important;
}

a {
  cursor: pointer;
}
